
.logo{
position: fixed;
left: 20px;
width: 80px;
top:10px;
cursor: pointer;
}
.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    top:5px;
    cursor: pointer;
}
.navbar{
    position: fixed;
    top:0;
    background-color: rgb(29, 35, 37);
    width: 100%;
    height: 40px;
    display:flex;
    justify-content: space-between;
    
}
#btn{
    position: fixed;
    right: 20px;
    top:4px;
    cursor: pointer;
    /* background-color: red; */
    background-color: rgb(29, 35, 37);
    border: none;
    outline: none;
    color: white;
    font-size: 15px;
    border-radius: 5px;
    padding: 7px 15px;
    cursor: pointer;
}
.search{
    position: fixed;
    right: 250px;
    top:4px;
    cursor: pointer;
    border: 1px solid rgb(130, 131, 132);
    outline: none;
    color:black;
    font-size: 15px;
    border-radius: 5px;
    padding: 5px 15px;
    cursor: pointer;
}
#btn1{
    position: fixed;
    right: 160px;
    top:4px;
    cursor: pointer;
    background-color: red;
    /* background-color: rgb(29, 35, 37); */
    border: none;
    outline: none;
    color: white;
    font-size: 15px;
    border-radius: 5px;
    padding: 7px 15px;
    cursor: pointer;
}
.navbar li{
    list-style: none;
    display: inline-block;
    margin: 0 20px;
    line-height: 40px;
    
}
.navbar ul{
    margin-left:200px;
    color:azure;
    cursor: pointer;
    text-decoration: none;
    color: white;
}
.navbar a{
    text-decoration: none;
    color: aliceblue;
}
.navbar li a:hover{
    color:brown
}
.navbar li:hover{
    color:brown
}