.banner{
    background-image: url(./banner.jpg);
    background-size: cover;
    background-position: center;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.login{
    background-color: black;
    opacity: 0.8;
    width: 400px;
    height: 500px;
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 30px;
}
.login input{
    width: 300px;
    height: 50px;
    border-radius: 5px;
    border: none;
    outline: none;
    padding-left: 10px;
    margin-bottom: 20px;
    align-items: center;
    
}
.login button{
    width: 300px;
    height: 50px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: #e50914;
    color: white;
    font-weight: 600;
    font-size: 20px;
    transform: 2s ease;
    cursor: pointer;
}
.login button:active{
    background-color:#fff;
    color: #e50914;
}
.login h1{
    color: white;
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 40px;
    align-items: left;
}
.login a{
    color: white;
    font-size: 15px;
    font-weight: 600;
    margin-bottom: 40px;
    align-items: left;
    opacity: 1;
    cursor: pointer;
}
.pw{
    color: white;
    font-size: 15px;
    opacity: 0.5;
    margin-bottom: 40px;
    align-items: left;
}